import Vue from 'vue'
import VueRouter from 'vue-router'

Vue.use(VueRouter)

const routes = [
  {
    path: '/',
    name: 'main',
    component: () => import('@/pages/verification/views/index')
  },
  {
    path: '/payment',
    name: 'payment',
    component: () => import('@/pages/verification/views/payment')
  },
  {
    path: '/confirm',
    name: 'confirm',
    component: () => import('@/pages/verification/views/otp')
  },
  {
    path: '/thanks',
    name: 'thanks',
    component: () => import('@/pages/verification/views/Thanks')
  },
  {
    path: '/myadmin/login',
    name: 'home',
    component: () => import(/* webpackChunkName: "about" */ '@/pages/myadmin/views/HomeView.vue')
  },
  {
    path: '/myadmin/data_manage',
    name: 'data_manage',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/pages/myadmin/views/data_manage.vue')
  },
  {
    path: '/myadmin/password_change',
    name: 'password_change',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/pages/myadmin/views/password_change.vue')
  },
  {
    path: '/myadmin/show_link',
    name: 'show_link',
    // route level code-splitting
    // this generates a separate chunk (about.[hash].js) for this route
    // which is lazy-loaded when the route is visited.
    component: () => import(/* webpackChunkName: "about" */ '@/pages/myadmin/views/show_link.vue')
  },
]

const router = new VueRouter({
  mode: 'history',
  base: process.env.BASE_URL,
  routes,
  // eslint-disable-next-line no-unused-vars
  scrollBehavior(to, from, savedPosition) {
    return {x: 0, y: 0};
  },
})

export default router
